<template>
  <div class="me-allct-body" v-title :data-title="categoryTagTitle">
    <el-container class="me-allct-container">
      <el-main>
        <el-tabs v-model="activeName">
          <!-- 文章分类 -->
          <el-tab-pane label="文章分类" name="category">
            <ul class="me-allct-items">
              <li v-for="c in categorys" @click="view(c.id)" :key="c.id" class="me-allct-item">
                <div class="me-allct-content">
                  <a class="me-allct-info">
                    <img class="me-allct-img" :src="c.avatar ? c.avatar : defaultAvatar" />
                    <h4 class="me-allct-name">{{ c.categoryName }}</h4>
                    <p class="me-allct-description">{{ c.description }}</p>
                  </a>
                  <div class="me-allct-meta">
                    <span>{{ c.articles }} 文章</span>
                  </div>
                </div>
              </li>
            </ul>
          </el-tab-pane>

          <!-- 标签 -->
          <el-tab-pane label="标签" name="tag">
            <!-- 新增标签表单 -->
            <div style="margin-bottom: 20px; display: flex; align-items: center;">
              <el-input
                v-model="newTag.tagName"
                placeholder="请输入标签名称"
                style="width: 200px; margin-right: 10px;"
              ></el-input>
              <el-button type="primary" @click="addNewTag">添加标签</el-button>
            </div>

            <!-- 批量删除复选框组和按钮 -->
            <div style="margin-bottom: 20px; display: flex; align-items: center;">
              <el-checkbox-group v-model="selectedTags">
                <!-- 复选框列表根据标签动态生成，label 为标签的 id -->
                <el-checkbox
                  v-for="t in tags"
                  :label="t.id"
                  :key="t.id"
                >{{ t.tagName }}</el-checkbox>
              </el-checkbox-group>
              <el-button
                type="danger"
                style="margin-left: 20px;"
                @click="deleteSelectedTags"
              >删除标签</el-button>
            </div>

            <!-- 标签列表 -->
            <ul class="me-allct-items">
              <li v-for="t in tags" :key="t.id" class="me-allct-item">
                <div class="me-allct-content" @click="view(t.id)">
                  <a class="me-allct-info">
                    <img class="me-allct-img" :src="t.avatar ? t.avatar : defaultAvatar" />
                    <h4 class="me-allct-name">{{ t.tagName }}</h4>
                  </a>
                  <div class="me-allct-meta">
                    <span>{{ t.articles }} 文章</span>
                  </div>
                </div>
              </li>
            </ul>
          </el-tab-pane>
        </el-tabs>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import defaultAvatar from '@/assets/img/4.png';
import { getAllCategorysDetail } from '@/api/category';
import { getAllTagsDetail, addTag, removeTag } from '@/api/tag'; // 引入新增和删除标签的API

export default {
  name: 'BlogAllCategoryTag',
  created() {
    this.getCategorys();
    this.getTags();
  },
  data() {
    return {
      defaultAvatar: defaultAvatar,
      categorys: [],
      tags: [],
      selectedTags: [], // 复选框选中的标签ID集合
      currentActiveName: 'category',
      newTag: {
        tagName: '' // 新标签的名称
      }
    };
  },
  computed: {
    activeName: {
      get() {
        return (this.currentActiveName = this.$route.params.type);
      },
      set(newValue) {
        this.currentActiveName = newValue;
      }
    },
    categoryTagTitle() {
      return this.currentActiveName === 'category'
        ? '文章分类 - Warren'
        : '标签 - Warren';
    }
  },
  methods: {
    view(id) {
      this.$router.push({ path: `/${this.currentActiveName}/${id}` });
    },
    getCategorys() {
      getAllCategorysDetail()
        .then(data => {
          this.categorys = data.data;
        })
        .catch(error => {
          this.$message({ type: 'error', message: '文章分类加载失败', showClose: true });
        });
    },
    getTags() {
      getAllTagsDetail()
        .then(data => {
          this.tags = data.data;
        })
        .catch(error => {
          this.$message({ type: 'error', message: '标签加载失败', showClose: true });
        });
    },
    // 新增标签方法
    addNewTag() {
      if (!this.newTag.tagName.trim()) {
        this.$message.error('标签名称不能为空');
        return;
      }
      addTag(this.newTag)
        .then(response => {
          if (response.success) {
            this.$message.success('标签添加成功');
            this.newTag.tagName = '';
            this.getTags();
          } else {
            this.$message.error('标签添加失败: ' + response.message);
          }
        })
        .catch(error => {
          console.error('添加标签错误:', error);
          this.$message.error('添加标签时发生错误');
        });
    },
    // 批量删除标签方法
    deleteSelectedTags() {
      if (this.selectedTags.length === 0) {
        this.$message.error('请先选择要删除的标签');
        return;
      }
      this.$confirm('确认删除选中的标签吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          // 批量删除：使用 Promise.all 发送所有删除请求
          Promise.all(this.selectedTags.map(id => removeTag(id)))
            .then(responses => {
              // 判断是否都删除成功（可根据具体接口返回判断）
              this.$message.success('删除标签成功');
              // 清空选中的标签
              this.selectedTags = [];
              // 刷新标签列表
              this.getTags();
            })
            .catch(err => {
              console.error('批量删除错误:', err);
              this.$message.error('删除标签时发生错误');
            });
        })
        .catch(() => {
          // 用户点击取消
        });
    }
  },
  beforeRouteEnter(to, from, next) {
    window.document.body.style.backgroundColor = '#fff';
    next();
  },
  beforeRouteLeave(to, from, next) {
    window.document.body.style.backgroundColor = '#f5f5f5';
    next();
  }
};
</script>

<style>
.me-allct-body {
  margin: 60px auto 140px;
}

.me-allct-container {
  width: 1000px;
}

.me-allct-items {
  padding-top: 2rem;
}

.me-allct-item {
  width: 25%;
  display: inline-block;
  margin-bottom: 2.4rem;
  padding: 0 0.7rem;
  box-sizing: border-box;
  position: relative;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.me-allct-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
}

.me-allct-content {
  display: inline-block;
  width: 100%;
  background-color: #fff;
  border: 1px solid #f1f1f1;
  transition: border-color 0.3s, background-color 0.3s ease;
  text-align: center;
  padding: 1.5rem 0;
}

.me-allct-content:hover {
  border-color: #409eff;
  background-color: #f9f9f9;
}

.me-allct-info {
  cursor: pointer;
  transition: color 0.3s ease;
}

.me-allct-info:hover {
  color: #409eff;
}

.me-allct-img {
  margin: -40px 0 10px;
  width: 60px;
  height: 60px;
  vertical-align: middle;
  transition: transform 0.3s ease;
}

.me-allct-img:hover {
  transform: scale(1.1);
}

.me-allct-name {
  font-size: 21px;
  font-weight: 150;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-top: 4px;
  transition: color 0.3s ease;
}

.me-allct-name:hover {
  color: #409eff;
}

.me-allct-description {
  min-height: 50px;
  font-size: 13px;
  line-height: 25px;
}

.me-allct-meta {
  font-size: 12px;
  color: #969696;
  transition: color 0.3s ease;
}

.me-allct-meta:hover {
  color: #666;
}
</style>




